.footer {
  display: flex;
  align-items: center;
  margin: -2.5rem -3.5rem;
  position: relative;
  /* margin-top: -7rem; */
}

.f-content {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  margin-top: 4rem;
  gap: 4rem;
  color: white;
}
.f-content span {
  font-size: 1.6rem;
  font-weight: 400;
}

